.header {
    display: flex;
    justify-content: space-between;
}

.logo {
    
    height: 3rem;
}

.header-menu {
    list-style: none;
    display: flex;
    gap: 2rem;
    color: white;
}

.header-menu>li:hover {
    cursor: pointer;
    color: var(--red);
}

